<template>
  <div class="content">
    <!--<div class="header">
    	<a @click="goUser" class="user-link link">
    		<img :src="avatar" style="border-radius: 50%;"  class="fullEle" />
    	</a>
    	<img class="logo-text" src="../../static/image/aoin.png" />
    	<span style="width: 0.85rem; height: 100%;"></span>
    </div>-->
    <div class="navbar">
    	<div @click="changeTag(item)" v-for="(item, index) in navbar" :class="currTags == item.id ? 'active' : ''"  class="nav-item">
    		<div class="text">{{item.name}}</div>
    	</div>
    </div>
    <div class="nav-content">
    	<div class="toys-content">
    		<Pagination :render="render" :needToken="false" :param="pagination" :autoload="false" ref="pagination" uri="/dm-api/home/tag" >
					<div class="home-data" v-show="firstTag == currTags">
		    		<div class="banner">
				    	<mt-swipe :auto="4000">
								<mt-swipe-item v-for="(item,index) in banner" :key="index">
									<div class="fullEle" @click="bannerLink(item)">
										<img :src="item.imgUrl" class="fullEle" />
									</div>
								</mt-swipe-item>
							</mt-swipe>
				    </div>
				    <div class="homebar">
				    	<a @click="navLink(item.link)" class="nav-item" v-for="(item,index) in homebar" :key="index">
				    			<img :src="item.icon"  />
				    	</a>
				    </div>
		    	</div>	
				<div class="toys-list" v-show="pagination.content.length>0">
			    	<div @click="enterRoom(item)" class="toys-item" v-for="(item, index) in pagination.content" :key="index">
			    		<img class="toys-img" :src="item.imgs[0]"  />
			    		<div class="toys-status" :class="[item.statusClass]">{{item.statusText}}</div>
			    		<div class="toys-info">
			    			<p class="join-count">
				    			<img class="icon" src="../../static/image/wd.png" />
				    			<span class="shadow-text">{{parseInt(item.price)}}</span>
				    		</p>
				    		<p class="toys-name shadow-text">{{item.name}}</p>
			    		</div>	
			    	</div>
			    </div>
				<div class="no_msg" v-show="pagination.content.length<1 && pagination.loadEnd">
					<img src="../../static/image/ewd.png"  />
					<div>没有商品信息~</div>
				</div>	
			</Pagination>
    	</div>
    </div>
    <div class="sign-content"  v-show="signShow && !guideShow">
    	<div class="sign-box">
    		<img class="bg" src="../../static/image/sign.png"  />
    		<div class="sign-text">
    			<div class="one-level">
    				<div class="sign-item" :class="{'active': signItem_1.status}">
    					<div class="unsign">
    						<img class="icon" src="../../static/image/wd.png"  />
    						<p class="shadow-text">{{signItem_1.points}}</p>
    					</div>
    					<div class="shadow-text signed">
    						已<br />签
    					</div>
    				</div>
    				<img class="arror" src="../../static/image/566gg.png"  />
    				<div class="sign-item" :class="{'active': signItem_2.status}">
    					<div class="unsign">
    						<img class="icon" src="../../static/image/wd.png"  />
    						<p class="shadow-text">{{signItem_2.points}}</p>
    					</div>
    					<div class="shadow-text signed">
    						已<br />签
    					</div>
    				</div>
    				<img class="arror" src="../../static/image/566gg.png"  />
    				<div class="sign-item" :class="{'active': signItem_3.status}">
    					<div class="unsign">
    						<img class="icon" src="../../static/image/wd.png"  />
    						<p class="shadow-text">{{signItem_3.points}}</p>
    					</div>
    					<div class="shadow-text signed">
    						已<br />签
    					</div>
    				</div>
    			</div>
    			<div class="two-level">
    				<img src="../../static/image/344c.png"  />
    			</div>
    			<div class="one-level">
    				<div class="sign-item" :class="{'active': signItem_6.status}">
    					<div class="unsign">
    						<img class="icon" src="../../static/image/wd.png"  />
    						<p class="shadow-text">{{signItem_6.points}}</p>
    					</div>
    					<div class="shadow-text signed">
    						已<br />签
    					</div>
    				</div>
    				<img class="arror" src="../../static/image/gghhh.png"  />
    				<div class="sign-item" :class="{'active': signItem_5.status}">
    					<div class="unsign">
    						<img class="icon" src="../../static/image/wd.png"  />
    						<p class="shadow-text">{{signItem_5.points}}</p>
    					</div>
    					<div class="shadow-text signed">
    						已<br />签
    					</div>
    				</div>
    				<img class="arror" src="../../static/image/gghhh.png"  />
    				<div class="sign-item" :class="{'active': signItem_4.status}">
    					<div class="unsign">
    						<img class="icon" src="../../static/image/wd.png"  />
    						<p class="shadow-text">{{signItem_4.points}}</p>
    					</div>
    					<div class="shadow-text signed">
    						已<br />签
    					</div>
    				</div>
    			</div>
    			<div class="three-level">
    				<img src="../../static/image/34dd.png"  />
    				<div class="sign-item" :class="{'active': signItem_7.status}">
    					<div class="unsign">
    						<img class="icon" src="../../static/image/wd.png"  />
    						<p class="shadow-text">{{signItem_7.points}}</p>
    					</div>
    					<div class="shadow-text signed">
    						已<br />签
    					</div>
    				</div>
    			</div>
    			<div class="sign-btn btn-hover" :class="{'signed': !signStatus}" @click="sign">
    				<span v-show="signStatus">我要签到</span>
    				<span v-show="!signStatus">已签到</span>
    			</div>
    		</div>
    	</div>
    </div>
    <div class="guide-tip" v-show="guideShow">
    	<img class="mask" src="../../static/image/guide1.png"  />
    	<div class="click-area" @click="guideOperate"></div>
    </div>
    
    
    <div class="float-content">
			<div class="share" @click="goUser">
    		<div class="float-icon"><img src="../../static/image/wanba-user.png"  /></div>
    		<p>个人中心</p>
    	</div>
    	<div class="share" @click="shareShow = true">
    		<div class="float-icon"><img src="../../static/image/wanba-share.png"  /></div>
    		<p>分享有礼</p>
    	</div>
    	<div class="save" @click="saveShow = true" v-show="platform != 2">
    		<div class="float-icon"><img src="../../static/image/wanba-save.png"  /></div>
    		
    		<p>收藏有礼</p>
    	</div>
    </div>
    <mt-popup v-model="saveShow" class="float-pop">
			<div class="float-box">
				<p class="tit">添加到桌面收藏可获得奖励</p>
				<img class="profit-icon" src="../../static/image/share@2x.png"  />
				<p class="profit-text shadow-text">
					<span>钻石</span>
					<span>x {{collectionNum}}</span>
				</p>
				<div class="float-btn" v-show="!saveConfirm" @click="save">收藏</div>
				<div class="float-btn" v-show="saveConfirm" @click="getSaveProfit">领取</div>
			</div>
		</mt-popup>
		<mt-popup v-model="shareShow" class="float-pop">
			<div class="float-box">
				<p class="tit">分享游戏可获得奖励</p>
				<img class="profit-icon" src="../../static/image/intergal.png"  />
				<p class="profit-text shadow-text">
					<span>积分</span>
					<span>x {{shareNum}}</span>
				</p>
				<div class="float-btn" v-show="!shareConfirm" @click="share">分享</div>
				<div class="float-btn" v-show="shareConfirm" @click="getShareProfit">领取</div>
			</div>
		</mt-popup>
    
  </div>
</template>

<script>
import { Toast, Indicator } from 'mint-ui'
export default {
  data () { 
    return {
    	homebar: [{
      	text: '签到',
      	icon: '../../static/image/wanba-sign.png',
      	link: '/signIn'
      },{
      	text: '娃娃盒',
      	icon: '../../static/image/wanba-toy.png',
      	link: '/toysBox'
      }],
      banner: [],
      
      navbar: [],
      toysList: [],
      currTags: null,
      firstTag: null,
      pagination: {
	        content: [],
	        loadEnd: false,
	        data: {
	        	page: 1,
	        	pageSize: 20
	        }
	    },
	    
	    avatar: '../../static/image/vvv.png',  //头像
	    
	    musicSwitch: true,  //背景音乐
	    
	    guideShow: false,   //新手指引
	    
	    signShow: false,   //签到
	    signItem_1: {},
	    signItem_2: {},
	    signItem_3: {},
	    signItem_4: {},
	    signItem_5: {},
	    signItem_6: {},
	    signItem_7: {},
	    signStatus: null,
	    
	    
	    saveShow: false,    //收藏有礼
		  saveConfirm: false,
    	collectionNum: null,
    	
    	shareShow: false,   //分享有礼
    	shareNum: null,
    	shareConfirm: false,
	    platform: null
	    
    }
  },
  created() {
  	this.platform = window.OPEN_DATA.platform
  	const self = this
		mqq.invoke('ui', 'webviewCanScroll', {"enable" : false})
		mqq.invoke('ui','setOnShareHandler',function(type){
		    mqq.invoke('ui','shareMessage',{
		        title: '掌上抓物机',
		        desc: '真实在线远程操控抓娃娃机',
		        share_type: type,
		        share_url: window.OPEN_DATA.shareurl,
		        image_url: 'http://a.qpic.cn/psb?/V12zhLgs0wFmud/rglvmrzOorCAmDbEpSxLfEyIjagK*2frRbWR4eOMEPM!/b/dPQAAAAAAAAA&bo=yADIAMgAyAADEDU!',
		        back: true
		    },function(ret){
		    	if(ret.retCode == 0) {
		    		  self.shareShow = true
		    			self.shareConfirm = true
		    	}else {
		    		self.shareShow = false
		    	}
		    	
		        //result
		    });
		});
		window.mqq.invoke('ui','setOnAddShortcutHandler', {'callback':mqq.callback(self.save, false,true)})
  	this.$api.userTask().then(res => {
	    this.collectionNum = res.data.collection
		  this.shareNum = res.data.share
	  }, err => {
	        
	  })
		window.getOpenKey(function(msg){
	    self.$api.wanbaLogin({
    		platform: window.OPEN_DATA.platform,
    		openid: msg.data.openid,
    		openkey: msg.data.openkey
    	}).then(res => {
	        let accessToken = res.data.accessToken
					self.$token.refreshToken(accessToken.accessToken, accessToken.refreshToken, accessToken.expireTime)
					self.$storage.set('hx', {id: res.data.hxId, password: res.data.hxPwd})
					self.$storage.set('user', res.data)
					self.$storage.set('headUrl', res.data.avatar || '../../static/image/vvv.png')
				  self.$storage.set('isNew', res.data.firstLogin)
//					self.$storage.set('isNew', false)
					self.initData()
	    }, err => {
	    })
		});
  },
  mounted() {
  	
	  	
  },
  methods: {
  	initData() {
  		this.avatar = this.$storage.get('headUrl') || '../../static/image/vvv.png'
	  	//背景音乐
	  	if (this.$storage.get('music_switch') != null) {
				this.musicSwitch = this.$storage.get('music_switch')
			}
	  	this.$api.homeBanner().then(res => {
				this.banner = res.data
	    }, err => {
	    	  
			})
	  	this.signData()
	  	
	  	this.$api.homeTags().then(res => {
				this.navbar = res.data
				this.currTags = this.navbar[0].id
				this.firstTag = this.navbar[0].id
				this.tagChange(this.navbar[0].id)
	    }, err => {
	    	
	    })
  	},
  	navLink(link) {
  		if(link) {
  			this.$router.push(link)
  		}else {
  			Toast({
				  message: '暂未开通，尽请期待',
				  position: 'bottom',
				  duration: 1000
				});
  		}
	  		
  	},
  	//签到
  	signData() {
		this.$api.signList().then(res => {
			this.signItem_1 = res.data.list[0]
			this.signItem_2 = res.data.list[1]
			this.signItem_3 = res.data.list[2]
			this.signItem_4 = res.data.list[3]
			this.signItem_5 = res.data.list[4]
			this.signItem_6 = res.data.list[5]
			this.signItem_7 = res.data.list[6]
			this.signStatus = res.data.status
			this.signShow = !!res.data.status
	    }, err => {

	    })
	},
	sign() {
		this.$api.sign().then(res => {
			this.signStatus = 0
			this.signShow = false
			Toast({
			  message: '领取成功',
			  position: 'middle',
			  iconClass: 'toast-icon icon-success',
			  duration: 1500
			})
	    }, err => {
				this.signShow = false
	    })
	},
  	tagChange(id) {
  		this.pagination.data.page = 1
  		this.pagination.data.type = id
  		this.$refs.pagination.refresh() 
  	},
  	changeTag(item) {
  		if(this.currTags == item.id) {
  			return
  		}
  		this.tagChange(item.id)
  	},
  	bannerLink(room) {
  		if(room.type == 1) {
  			let url = '/room/' + room.machineSn + '/' + room.num + '/' + room.price + '/' + room.machineId + '/' + room.liveRoomCode
  			this.$router.push(url)
  		}else if(room.type == 2) {
				this.$router.push('/recharge')
  		}else {
//			window.location.href = room.url
  		}
  	},
  	render(res) {
			this.currTags = this.pagination.data.type
  			res.data.forEach((item) => {
  				if(item.status == 0) {
  					item.statusText = '空闲中'
  					item.statusClass = 'none'
  				}else if(item.status == 1) {
  					item.statusText = '游戏中'
  					item.statusClass = 'ing'
  				}else if(item.status == 2) {
  					item.statusText = '维护中'
  					item.statusClass = 'end'
  				}
        	this.pagination.content.push(item)
        })
  			this.guideShow = this.$storage.get('isNew')
  			
    },

    // 进入房间
    enterRoom(room) {
    		if(room.status == 2) {
	    		Toast({
						message: '机器维护中，请选择其他房间吧',
						position: 'middle',
						duration: 1500
					})
	    		return
	    	}
	      let url = '/room/' + room.machineSn + '/' + room.num + '/' + room.price + '/' + room.machineId + '/' + room.liveRoomCode
				this.$router.push(url)
    },
    //新手操作进入房间
    guideOperate() {
    	let room = this.pagination.content[0]
    	this.enterRoom(room)
			setTimeout(() => {
				this.guideShow = false
			},600)
   },
    goUser() {
    	this.$router.push('/user')
		},
		share() {
			mqq.ui.showShareMenu();
		},
		getShareProfit() {
			const self = this
			self.$api.userTaskComplete({
				type: 'share'
			}).then(res => {
				  self.shareConfirm = false
				  self.shareShow = false
		        Toast({
						  message: '分享成功',
						  position: 'middle',
						  iconClass: 'toast-icon icon-success',
						  duration: 2000
						})
		    }, err => {
		        
		    })
		},
		save() {
			const self = this
			mqq.ui.addShortcut({
		        action: 'web',
		        title: '掌上抓物机',
		        icon: 'http://a.qpic.cn/psb?/V12zhLgs0wFmud/rglvmrzOorCAmDbEpSxLfEyIjagK*2frRbWR4eOMEPM!/b/dPQAAAAAAAAA&bo=yADIAMgAyAADEDU!',
		        url: window.OPEN_DATA.jumpurl,
		        callback: function(ret){
		        	if(ret.result == 0) {
		        		self.saveShow = true
		        		self.saveConfirm = true
		        		
		        	}else {
		        		Toast({
								  message: '您已收藏过了哦！',
								  position: 'bottom',
								  duration: 2000
								});
			        	self.saveShow = false
		        	}
			        	
		        }
		    })
		},
		getSaveProfit() {
			const self = this
			self.$api.userTaskComplete({
				type: 'collection'
			}).then(res => {
				  self.saveConfirm = false
				  self.saveShow = false
		        Toast({
						  message: '收藏成功',
						  position: 'bottom',
						  duration: 2000
						});
		    }, err => {
		        self.saveConfirm = false
				    self.saveShow = false
		    })
		}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.float-pop{
	border-radius: 0.2rem;
	overflow: hidden;
}
.float-box{
	width: 6rem;
	padding: 0.3rem 0.2rem;
	text-align: center;
	
	.tit{
		padding: 0.15rem;
		color: #000000;
		font-size: 0.32rem;
	}
	.profit-icon{
		display: block;
		width: 3.1rem;
		margin: 0 auto;
	}
	.profit-text{
		color: #fff;
		font-size: 0.3rem;
		text-shadow: 2px 0 1px #000,0 2px 1px #000,-2px 0 1px #000,0 -2px 1px #000;
		*filter: Glow(color=#000, strength=1);
	}
	.float-btn{
		width: 4rem;
		height: 0.66rem;
		line-height: 0.66rem;
		background: #f97295;
		border-radius: 0.66rem;
		margin: 0.5rem auto 0.2rem;
		color: #fff;
		font-size: 0.3rem;
	}
}
.float-content{
	position: fixed;
	z-index: 100;
	right: 0.2rem;
	bottom: 0.8rem;
	text-align: center;
	.float-icon{
		margin: 0 auto;
		width: 0.7rem;
		height: 0.7rem;
		img{
			display: block;
			width: 100%;
			height: 100%;
		}
	}
	&>div{
		text-align: center;
		margin-bottom: 0.3rem;
		color: #000;
		p{
			padding-top: 0.1rem;
			font-size: 0.24rem;
		}
	}
}
.sign-content{
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 100;
	background: rgba(55,55,55,0.6);
	.sign-box{
		position: absolute;
		width: 6.5rem;
		transform: translate(-50%, -55%);
		left:50%;
		top:50%;
		.bg{
			display: block;
			width: 100%;
		}
		.sign-text{
			position: absolute;
			top: 3.1rem;
			bottom: 0;
			left: 0;
			right: 0;
			z-index: 110;
			padding: 0 1.06rem;
		}
	}
}

.guide-tip{
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 1000;
	.mask{
		position: absolute;
		width: 100%;
		left: 0;
		top: -1.18rem;
		z-index: 3001;
	}
}
.click-area{
	width: 3.45rem;
	height: 3.45rem;
	position: absolute;
	left: 0.2rem;
	top: 6rem;
	z-index: 3005;
}
.header{
	height: 0.85rem;
	background: #ffe86e;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 0.15rem;
	.link{
		width: 0.85rem;
		height: 0.85rem;
		/*background-position: center;
		background-repeat: no-repeat;
		background-size: 55%;*/
		position: relative;
		padding: 0.15rem;
	}
	/*.user-link{
		background-image: url(../../static/image/vvv.png);
	}
	.news-link{
		background-image: url(../../static/image/sdde.png);
		background-size: 50%;
		.count{
			position: absolute;
			padding: 0 0.06rem;
			min-width: 0.36rem;
			height: 0.36rem;
			border-radius: 50%;
			background: #f43c5f;
			color: #fff;
			text-align: center;
			line-height: 0.36rem;
			font-size: 0.24rem;
			top: 0.1rem;
			right: 0.06rem;
		}
	}*/
	.logo-text{
		height: 0.46rem;
	}
}
.navbar{
	height: 0.9rem;
	padding-top: 0.14rem;
	display: flex;
	background: #ffe86e;
	.nav-item{
		flex: 1;
		display: flex;
		justify-content: center;
		line-height: 0.76rem;
		.text{
			color: #cb8c32;
			font-size: 0.28rem;
			transition: all 0.1s;
		}
		&.active{
			.text{
				color: #704a06;
				padding: 0 0.06rem;
				border-bottom: 2px solid #704a06;
			}
		}
	}
}
.nav-content{
	position: absolute;
	width: 100%;
	left: 0;
	top: 0.9rem;
	bottom: 0;
	overflow-y: auto;
	display: flex;
	flex-direction: column;
}
.banner{
	height: 2.7rem;
	background: #ddd;
}
.homebar{
	/*height: 1.6rem;*/
	display: flex;
	padding: 0 0.08rem;
	.nav-item{
		flex: 1;
		padding:0.2rem 0.08rem 0;
			img{
				display: block;
				width: 100%;
      }
	}
}
.toys-content{
	flex: 1;
	overflow-y: auto;
	-webkit-overflow-scrolling : touch;
}
.toys-list{
	padding: 0.2rem 0.1rem 0;
	display: flex;
		flex-wrap: wrap;
	.toys-item{
		margin: 0 0.1rem 0.2rem;
		width: 3.45rem;
		height: 3.45rem;
		background: #fff;
		position: relative;
		border-radius: 0.3rem;
		overflow: hidden;
		
		.toys-img{
			border-radius: 0.3rem;
			position: absolute;
			top:50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 100%;
			height: 100%;
		}
		.toys-status{
			position: absolute;
			right: 0.2rem;
			top: 0.2rem;
			height: 0.34rem;
			line-height: 0.34rem;
			border-radius: 0.17rem;
			color:#fff;
			padding: 0 0.12rem;
			z-index: 3;
			font-size: 0.22rem;
			&.ing{
				background: #ff556e;
			}
			&.none{
				background: #3eb428;
			}
			&.end{
				background: #c7c8c8;
			}
		}
		.toys-info{
			position: absolute;
			left: 0.2rem;
			right: 0.2rem;
			bottom: 0.2rem;
			z-index: 3;
			color: #fff;
			font-weight: 700;
			font-size: 0.28rem;
			.join-count{
				display: flex;
				align-items: center;
				font-weight: normal;
				padding-bottom: 0.1rem;
				.icon{
					width: 0.26rem;
					margin-right: 0.16rem;
				}
			}
		}
	}
}



.one-level{
	display: flex;
	align-items: center;
	justify-content: center;
}
.arror{
	width: 0.44rem;
	margin: 0 0.1rem;
}
.sign-item{
	width: 0.9rem;
	height: 1.04rem;
	
	background-image: url(../../static/image/233fccc.png);
	background-size: 100% 100%;
	position: relative;
	.icon{
		display: block;
		margin: 0 auto;
		width: 0.3rem;
		margin-top: 0.1rem;
	}
	p{
		color: #fff;
		text-align: center;
		font-size: 0.28rem;
	}
	&.active{
		background-image: url(../../static/image/sw3444.png);
		display: block;
		.signed{
			display: none;
		}
		.unsign{
			display: block;
		}
	}
	.unsign{
		display: none;
	}
	.signed{
		display: block;
		color: #fff;
		font-size: 0.28rem;
		line-height: 1.3;
		position: absolute;
		left: 50%;
		top: 50%;
		width: 100%;
		text-align: center;
		transform: translate(-50%, -50%);
	}
}
.two-level{
	display: flex;
	justify-content: flex-end;
	padding: 0.14rem 0.54rem;
	img{
		height: 0.44rem;
	}
}
.three-level{
	padding-top: 0.6rem;
	position: relative;
	display: flex;
	justify-content: center;
	&>img{
		position: absolute;
		top: 0.14rem;
		width: 0.94rem;
		left: 0.64rem;
	}
}
.sign-btn{
	width: 2.5rem;
	height: 0.7rem;
	border-radius: 0.2rem;
	text-align: center;
	line-height: 0.7rem;
	color: #FFFFFF;
	background: #f97295;
	margin: 0.44rem auto 0;
	&.signed{
		background: #ddd;
		color: #efefef;
	}
}
</style>



